@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Pacifico&display=swap);
.modal-primary-container{position:fixed;display:flex;padding:20px 10px;justify-content:center;top:0;left:0;right:0;bottom:0;z-index:9999999;background:rgba(0,0,0,.2);overflow:auto;transition:.05s all ease}.modal-primary-container .modal-primary{display:flex;flex-direction:column;min-height:100px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;flex:1 1;padding:28px 40px;border-radius:15px;background:#fff;position:relative;max-width:765px}.modal-primary-container .search-modal,.modal-primary-container .user-role-modal{position:relative;top:80px}.modal-primary-container .search-modal .modal-close-button,.modal-primary-container .user-role-modal .modal-close-button{position:absolute;right:20px;top:15px;background:#fff;padding:10px;z-index:2;border:none}.modal-primary-container .search-modal .modal-close-button i,.modal-primary-container .user-role-modal .modal-close-button i{font-size:2.2em;color:#000;margin:0px}.modal-primary-container .goback{margin-left:1rem}.modal-primary-container .gobackbtn{border-color:#000;margin-left:1rem}
.video-part{width:100%;margin:3.5rem auto}.concept{margin:.2rem 5rem}.concept h1{margin-top:1rem;font-size:2.5rem;font-weight:600;color:rgba(46,43,43,.76);text-shadow:0px 4px 4px rgba(0,0,0,.25);font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif}.grade{font-weight:500;padding:.5rem 2rem;margin-right:1rem;background-color:#d3d3d3}.selected{background-color:#f5f5f5}.selected-tab{margin-top:0rem;border-top-style:solid;border-color:#f5f5f5;background-color:#fff}.slide img{margin:.5rem auto}.slide{-webkit-transform:scale(0.6);transform:scale(0.6);transition:-webkit-transform 300ms;transition:transform 300ms;transition:transform 300ms, -webkit-transform 300ms;opacity:.9}.slide .videos-img{border-radius:22px;width:15rem;height:18rem}.activeSlide{-webkit-transform:scale(1);transform:scale(1);opacity:1}.activeSlide img{box-shadow:1px 1px 4px 6px rgba(80,73,73,.384)}.arrow{background-color:transparent;position:absolute;cursor:pointer;z-index:10;font-size:2rem}.arrow svg{transition:color 300ms}.arrow svg:hover{color:#68edff}.next{right:0%;top:40%}.prev{left:0%;top:40%}.class-btn{margin-top:1rem;margin-bottom:0rem}.tutor-cards{width:85%;margin:30px auto;padding-bottom:50px;margin-bottom:0px;display:grid;grid-template-rows:repeat(1, 19rem);grid-template-columns:repeat(auto-fit, minmax(22rem, 32rem));justify-content:center;grid-row-gap:3rem;grid-column-gap:3rem;grid-auto-rows:19rem;background-color:#fff}.tutor-cards .live-card{background:#add8e6;box-shadow:0px 4px 4px rgba(0,0,0,.25);border-radius:22px}.tutor-cards .live-card .live{background-color:red;display:inline-block;color:#fff;font-size:1.1rem;font-weight:500;border-radius:20px;padding:.2rem .7rem;text-transform:uppercase;float:right;margin-right:1rem}.tutor-cards .live-card .content{display:grid;grid-template-rows:13rem;grid-template-columns:minmax(8rem, 12rem) minmax(12rem, 1fr);justify-content:center;grid-column-gap:2rem}.tutor-cards .live-card .content img{width:10rem;height:12rem;border-radius:20px;margin-top:1rem;float:right}.tutor-cards .live-card .content .header{padding-top:1.3rem;align-items:center;text-align:left;line-height:1.2}.tutor-cards .live-card .content .header .class-no{font-weight:bold;font-size:1.3rem}.tutor-cards .live-card .content .header .class-no{display:inline-block}.tutor-cards .live-card .content .header .tutor-name{font-size:1.8rem;font-weight:bold;background-color:#fff;margin-top:1.5rem;margin-bottom:.3rem;padding-left:.2rem}.tutor-cards .live-card .content .header .subject-live{font-size:1.3rem;font-weight:600;margin-top:1rem;text-transform:capitalize}.tutor-cards .live-card .live-card-buttons .joinbtn{padding:.2rem .5rem;float:right;margin-right:1.2rem;margin-top:.5rem;font-size:1.1rem;font-weight:bold}button:hover{cursor:pointer;-webkit-transform:scale(1.05);transform:scale(1.05);transition:.3s ease}.livenow{font-size:2rem;font-weight:bold;margin-top:.5rem}.livenow .live-now-div{display:inline-block}.livenow .live-all{display:inline-block;color:#fe800c;margin-right:4rem;float:right}.livenow .live-all h4{margin-top:.5rem;font-size:1.3rem;font-weight:bold}.livenow .live-all:hover{cursor:pointer}@media(max-width: 480px){.concept{margin:.2rem 1rem !important}.concept h1{font-size:1.5rem !important}.video-part{width:100% !important}.slide img{width:10rem !important;height:8rem !important}.content img{width:8rem !important;height:10rem !important;border-radius:20px !important;margin-top:1rem;float:right !important}.next{top:30% !important}.prev{top:30% !important}}@media(max-width: 1039.98px){.slide img{width:14rem !important;height:12rem !important}.next{right:0%;top:40% !important}.prev{left:0%;top:40% !important}}
body{margin:0;font-family:"Raleway",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#fff;margin:0;padding:0}body .my-navbar{background-color:#fff;padding:10px 6%;transition:all .3s ease;margin-bottom:15px;border-bottom-style:groove}body .my-navbar .navbar-brand{font-family:"Pacifico",cursive;text-transform:capitalize;font-size:2rem;color:#000}body .my-navbar .dot{color:red;font-size:1rem}body .my-navbar .nav-item{font-size:1.1rem;font-weight:bold;color:#000;margin-left:.8rem}body .my-navbar .log-in{border-radius:22px;padding:.3rem 2rem;margin-left:1rem;font-weight:bold;background:linear-gradient(270deg, orangered, rgba(219, 17, 192, 0.808));color:#fff;text-align:center}
